import React, { Component } from 'react'
import { connect } from 'react-redux'

import { Layout,Card,Button,Row, Col, Menu, Breadcrumb } from 'antd';


import {getHandleShare,getHandleArtifact,getHandleCapsule} from '../../../services'
import {withRouter} from 'react-router-dom'
import {Route,Switch,Redirect } from 'react-router-dom'

// import {allInfo} from '../routes'
import {allInfo,uploadInfo} from '../../../routes'

import './userManage.less'

const { Header, Content, Footer } = Layout;

// import  from '../../../services'
// const { Header, Footer, Sider, Content } = Layout;

const menus = allInfo.filter(routes =>routes.isNav === true)


// const mapState = state =>({
//     isLogin: state.user.isLogin
// })

// @connect(mapState)
class UserManageInfo extends Component {
  onMenuClick = ({key})=>{
    this.props.history.push(key)
  } 

    constructor(){
      super()
      this.state ={
        // 专题
          sharePart:{
            sharePartData:[],
            sharePartIsLoading:false,
            shareParColumns:[],
          },

          // 作品
          artifact:{
            artifactData:[],
            artifactIsLoading:false,
            artifactColumns:[],
          },

          // 时讯
          capsule:{
            capsuleData:[],
            capsuleIsLoading:false,
            capsuleColumns:[],
          }
      }
    }
   
    render() {
      const selectedkeyArr = this.props.location.pathname.split('/')
      selectedkeyArr.length = 4
        return (
          <div>
            <Layout>
              <Header  className="myNormalFont myUserDynamicHead" >
                <div className="logo" />
                <Menu
                  theme="light"
                  mode="horizontal"
                  selectedKeys={ [selectedkeyArr.join('/')]}
                  className='myUserMenu'
                  onClick={this.onMenuClick}
                >
                  {
                    menus.map(item =>{
                      return <Menu.Item key={item.pathname}> {item.title} </Menu.Item>
                    })
                  }
                </Menu>
              </Header>
              <Content className="myUserContent">
                <Switch>
                    {
                      allInfo.map(route=>{
                        return <Route
                            key={route.pathname}
                            path={route.pathname}
                            exact={route.exact}
                            render = {(routeProps)=>{
                                      return <route.component {...routeProps} />
                            }}
                        />
                      })
                    }
                    
                    <Redirect to={allInfo[0].pathname} from='/admin/info' exact  />
                    <Redirect  to='/404' />
                </Switch>
              </Content>
              {/* <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer> */}
            </Layout>
          </div>
        )
    }
}

export default UserManageInfo
